import Home from '@/components/HomePage.vue'
import Workshop from '@/components/WorkshopPage.vue'
import Publications from '@/components/PublicationsPage.vue'
import { createRouter, createWebHistory } from 'vue-router';

 
const routers = [
    {
      path: '/',
      name: 'HomePageVue',
      component: Home
    },
    {
      path: '/demo2',
      name: 'WorkshopPage',
      component: Workshop
    },
    {
      path: '/demo1',
      name: 'PublicationsPage',
      component: Publications
    }
]

const router = createRouter({
  history: createWebHistory(),
  routes: routers, // `routes: routes` 
})

export default router;

